<template>
  <div class="p-d-inline-block div-card">
    <Card>
      <template #title>
        Simple List element
        <p class="card-short-data">
          <i class="pi pi-eye"></i>
          {{ data.views }}
        </p>
      </template>
      <template #header>
        <router-link :to="{ name: 'Anunt', params: { id: data.id } }">
          <img
            :alt="data.altText"
            :src="
              `${publicPath}` + 'images/cardPresentationImages/' + data.image
            "
            class="card-image"
          />
        </router-link>
      </template>
      <template #content>
        <p>
          {{ data.description }}
        </p>
      </template>
    </Card>
  </div>
</template>

<script>
export default {
  name: "desktopListComponent",
  props: ["data"],
  data() {
    return {
      publicPath: process.env.BASE_URL,
    };
  },
};
</script>
<style scoped>
.card-image {
  width: auto !important;
  height: 200px;
}
.p-card-header {
  text-align: center;
}
.div-card {
  width: 100%;
}
.card-short-data {
  font-weight: 400;
  font-size: 16px;
  margin: 0 0 0 0%;
  color: #2f4550;
}
</style>