<template>
  <div class="p-d-inline-block div-card">
    <Card>
      <template #header>
        <div class="card-header p-p-3">
          <router-link :to="{ name: 'Anunt', params: { id: data.id } }">
            <img
              :alt="data.altText"
              :src="
                `${publicPath}` + 'images/cardPresentationImages/' + data.image
              "
              class="card-image"
            />
          </router-link>
        </div>
      </template>
      <template #title>
        Simple Card
        <div>
          <p class="card-short-data">
            <i class="pi pi-eye"></i>
            {{ data.views }}
          </p>
        </div>
      </template>
      <template #content>
        <p>
          {{ data.description }}
        </p>
      </template>
    </Card>
  </div>
</template>

<script>
export default {
  name: "desktopCardComponent",
  props: ["data"],
  data() {
    return {
      publicPath: process.env.BASE_URL,
    };
  },
};
</script>
<style scoped>
.card-image {
  width: auto !important;
  height: 200px;
}
.card-header {
  text-align: center;
}
.div-card {
  width: 100%;
}
.p-card {
  margin: 0.5rem;
  border: 1px solid #dee2e6;
}
.card-short-data {
  font-weight: 400;
  font-size: 16px;
  margin: 0 0 0 0%;
  color: #2f4550;
}
</style>