<template>
  <div>
    <addListingMobile v-if="isMobileOnly"></addListingMobile>
    <addListingTablet v-if="isTablet"></addListingTablet>
    <!-- To update to      <addListingDesktop v-if="!isMobile"></addListingDesktop>  -->
    <addListingDesktop></addListingDesktop>
  </div>
</template>

<script>
import addListingMobile from "../components/Mobile/addListingMobile";
import addListingTablet from "../components/Tablet/addListingTablet";
import addListingDesktop from "../components/Desktop/addListingDesktop";
import { isMobileOnly, isTablet, isMobile } from "mobile-device-detect";
export default {
  name: "AddListing",
  data() {
    return {
      isMobileOnly: isMobileOnly,
      isTablet: isTablet,
      isMobile: isMobile,
    };
  },
  methods: {},
  components: {
    addListingMobile,
    addListingTablet,
    addListingDesktop,
  },
};
</script>