<template>
  <div class="p-grid nested-grid">
    <div class="p-col-2"></div>
    <div class="p-col-8">
      <div class="p-grid">
        <div class="p-col-12">
          <div class="p-d-inline-block div-card">
            <Card>
              <template #header>
                <div>
                  <img
                    :alt="data.altText"
                    :src="
                      `${publicPath}` +
                      'images/cardPresentationImages/' +
                      data.image
                    "
                    class="card-image-listing-type"
                  />
                </div>
              </template>
              <template #title>
                <h2 class="listing-owner">{{ data.title }}</h2>
                <p class="listing-short-data">
                  <i class="pi pi-user"></i> {{ data.createdByFirstName }}
                  {{ data.createdByLastName }}
                </p>
                <p class="listing-short-data">
                  <i class="pi pi-map-marker"></i>
                  {{ data.city }}, {{ data.locality }}
                </p>
                <p class="listing-short-data">
                  <i class="pi pi-bars"></i>
                  {{ data.categoryTitle }}
                </p>
              </template>
              <template #content>
                <div class="p-grid">
                  <div class="p-col-6">
                    <h2 class="listing-description-title">Descriere</h2>
                    <p class="listing-description-content">
                      {{ data.description }}
                    </p>
                  </div>
                  <div class="p-col-6">
                    <h2 class="listing-description-title">Locatie</h2>
                    <br />
                    <img
                      :alt="data.altText"
                      :src="`${publicPath}` + 'images/testMap.png'"
                      class="card-image-listing-map"
                    />
                  </div>
                </div>
              </template>
            </Card>
          </div>
        </div>
      </div>
    </div>
    <div class="p-col-2"></div>
  </div>
</template>

<script>
export default {
  name: "listingElementDesktop",
  props: ["data"],
  components: {},
  data() {
    return {
      publicPath: process.env.BASE_URL,
    };
  },
};
</script>
<style scoped>
.card-image-listing-type {
  width: auto !important;
  height: 200px;
}
.card-image-listing-map {
  width: 100% !important;
  height: auto;
}
.div-card {
  width: 100%;
}
.listing-owner {
  margin: 2% 0px 0px 0px;
}
.listing-short-data {
  font-weight: 400;
  font-size: 16px;
  margin: 0 0 0 0%;
  color: #2f4550;
}
.listing-description-title {
  margin: 0 0 0 0;
  letter-spacing: 0.011em;
}

.listing-description-content {
  margin: 2% 0px 0px 0%;
  line-height: 1.47384;
  font-weight: 300;
  letter-spacing: 0.018em;
  font-weight: 300;
  font-size: 18px;
  color: #2f4550;
}
</style>