        <DataView
          :value="elements"
          :layout="layout"
          :paginator="true"
          :rows="9"
          :sortOrder="sortOrder"
          :sortField="sortField"
          @page="onPage($event)"
        >
          <template #header>
            <div class="p-grid p-nogutter">
              <div class="p-col-6" style="text-align: left">
                <Dropdown
                  v-model="sortKey"
                  :options="sortOptions"
                  optionLabel="label"
                  placeholder="Sort By Price"
                  @change="onSortChange($event)"
                />
              </div>
              <div class="p-col-6" style="text-align: right">
                <DataViewLayoutOptions v-model="layout" />
              </div>
            </div>
          </template>

          <template #list="slotProps">
            <div class="p-col-12">
              <desktopListComponent :data="slotProps.data">
              </desktopListComponent>
            </div>
          </template>

          <template #grid="slotProps">
            <div class="p-col-12 p-md-4">
              <desktopCardComponent
                :data="slotProps.data"
              ></desktopCardComponent>
            </div>
          </template>
        </DataView>
      </div>
    </div>
    <div class="p-col-2"></div>
  </div>
</template>

