<template>
  <div class="card">
    <Carousel
      :value="data"
      :numVisible="3"
      :numScroll="1"
      :responsiveOptions="responsiveOptions"
      class="custom-carousel"
      :circular="true"
      :autoplayInterval="3000"
    >
      <template #header>
        <h5>Categorii Populare</h5>
      </template>
      <template #item="slotProps">
        <div class="product-item">
          <div class="product-item-content">
            <div class="p-mb-3">
              <router-link :to="{ name: 'Anunt', params: { id: data.id } }">
                <img
                  :alt="data.altText"
                  :src="
                    `${publicPath}` +
                    'images/cardPresentationImages/' +
                    slotProps.data.image
                  "
                  class="carousel-card-image"
                />
              </router-link>
            </div>
            <div>
              <h4 class="p-mb-1">{{ slotProps.data.name }}</h4>
            </div>
          </div>
        </div>
      </template>
    </Carousel>
  </div>
</template>
<script>
export default {
  name: "desktopCarouselComponent",
  props: ["data"],
  data() {
    return {
      publicPath: process.env.BASE_URL,
      responsiveOptions: [
        {
          breakpoint: "1024px",
          numVisible: 3,
          numScroll: 3,
        },
        {
          breakpoint: "600px",
          numVisible: 2,
          numScroll: 2,
        },
        {
          breakpoint: "480px",
          numVisible: 1,
          numScroll: 1,
        },
      ],
    };
  },
};
</script>
<style scoped>
.carousel-card-image {
  height: 200px;
}
</style>
